.api-client {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-top: 2rem;
}

.request-section,
.response-section {
  background: #f8f9fa;
  border-radius: 10px;
  padding: 1.5rem;
  border: 2px solid transparent;
  transition: all 0.3s ease;
}

.request-section:hover,
.response-section:hover {
  border-color: #667eea;
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.1);
}

.request-config {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.method-url {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.method-select {
  padding: 0.75rem 1rem;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  background: white;
  font-weight: bold;
  color: #667eea;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 100px;
}

.method-select:hover {
  border-color: #667eea;
  box-shadow: 0 2px 8px rgba(102, 126, 234, 0.2);
}

.method-select:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.url-input {
  flex: 1;
  padding: 0.75rem 1rem;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.url-input:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.send-button {
  padding: 0.75rem 1.5rem;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.send-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.send-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  transform: none;
}

.headers-section {
  border: 1px solid #e9ecef;
  border-radius: 8px;
  padding: 1rem;
  background: white;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.section-header h3 {
  margin: 0;
  color: #333;
  font-size: 1.1rem;
}

.add-button {
  padding: 0.5rem 1rem;
  background: #28a745;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.add-button:hover {
  background: #218838;
  transform: translateY(-1px);
}

.headers-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.header-row {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

.header-key,
.header-value {
  flex: 1;
  padding: 0.5rem;
  border: 1px solid #e9ecef;
  border-radius: 4px;
  font-size: 0.9rem;
}

.header-key:focus,
.header-value:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 2px rgba(102, 126, 234, 0.1);
}

.remove-button {
  padding: 0.5rem;
  background: #dc3545;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1.2rem;
  line-height: 1;
  transition: all 0.3s ease;
  min-width: 32px;
}

.remove-button:hover {
  background: #c82333;
  transform: scale(1.1);
}

.body-section {
  border: 1px solid #e9ecef;
  border-radius: 8px;
  padding: 1rem;
  background: white;
}

.body-section h3 {
  margin: 0 0 1rem 0;
  color: #333;
  font-size: 1.1rem;
}

.body-textarea {
  width: 100%;
  padding: 1rem;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  line-height: 1.5;
  resize: vertical;
  min-height: 150px;
}

.body-textarea:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.error-message {
  background: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.error-message h3 {
  margin: 0 0 0.5rem 0;
  color: #721c24;
}

.error-message p {
  margin: 0;
  color: #721c24;
  font-family: 'Courier New', monospace;
}

.response-container {
  background: white;
  border-radius: 8px;
  border: 1px solid #e9ecef;
  overflow: hidden;
}

.response-header {
  background: #f8f9fa;
  padding: 1rem;
  border-bottom: 1px solid #e9ecef;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.response-header h3 {
  margin: 0;
  color: #333;
  font-size: 1.1rem;
}

.response-meta {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.status-code {
  font-weight: bold;
  font-size: 1rem;
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  background: rgba(0, 0, 0, 0.1);
}

.response-time {
  color: #6c757d;
  font-size: 0.9rem;
  font-weight: 500;
}

.response-tabs {
  padding: 1rem;
}

.response-headers {
  margin-bottom: 1.5rem;
}

.response-headers h4 {
  margin: 0 0 0.75rem 0;
  color: #333;
  font-size: 1rem;
}

.headers-display {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  padding: 0.75rem;
  max-height: 200px;
  overflow-y: auto;
}

.header-item {
  padding: 0.25rem 0;
  border-bottom: 1px solid #e9ecef;
  font-size: 0.9rem;
  font-family: 'Courier New', monospace;
}

.header-item:last-child {
  border-bottom: none;
}

.header-item strong {
  color: #667eea;
}

.response-body {
  border-top: 1px solid #e9ecef;
  padding-top: 1rem;
}

.body-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.75rem;
}

.body-header h4 {
  margin: 0;
  color: #333;
  font-size: 1rem;
}

.copy-button {
  padding: 0.5rem 1rem;
  background: #6c757d;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: all 0.3s ease;
}

.copy-button:hover {
  background: #5a6268;
  transform: translateY(-1px);
}

.response-data {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  padding: 1rem;
  margin: 0;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  line-height: 1.5;
  white-space: pre-wrap;
  word-wrap: break-word;
  max-height: 400px;
  overflow-y: auto;
  color: #333;
}

/* CORS Notice */
.cors-notice {
  background: #fff3cd;
  border: 1px solid #ffeaa7;
  border-radius: 8px;
  padding: 1rem;
  margin-top: 1rem;
  text-align: left;
}

.cors-notice h4 {
  margin: 0 0 0.5rem 0;
  color: #856404;
  font-size: 1rem;
}

.cors-notice p {
  margin: 0 0 0.5rem 0;
  color: #856404;
  font-size: 0.9rem;
}

.cors-notice ul {
  margin: 0.5rem 0;
  padding-left: 1.5rem;
  color: #856404;
  font-size: 0.9rem;
}

.cors-notice li {
  margin-bottom: 0.25rem;
}

.example-urls {
  margin-top: 0.75rem;
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  align-items: center;
}

.example-urls strong {
  color: #856404;
  font-size: 0.9rem;
  margin-right: 0.5rem;
}

.example-btn {
  padding: 0.25rem 0.75rem;
  background: #856404;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.8rem;
  transition: all 0.3s ease;
}

.example-btn:hover {
  background: #6c4a03;
  transform: translateY(-1px);
}

/* CORS Options */
.cors-options {
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 8px;
  padding: 1rem;
  margin-bottom: 1rem;
}

.cors-checkbox {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  margin-bottom: 0.5rem;
}

.cors-checkbox input[type="checkbox"] {
  width: 16px;
  height: 16px;
  cursor: pointer;
}

.cors-checkbox span {
  font-weight: 500;
  color: #333;
  font-size: 0.9rem;
}

.cors-info {
  margin-top: 0.5rem;
}

.cors-info small {
  color: #6c757d;
  font-size: 0.8rem;
  line-height: 1.4;
}

/* Disabled state for method select when proxy is enabled */
.method-select:disabled {
  opacity: 0.6;
  cursor: not-allowed;
  background: #f8f9fa;
}

/* Responsive design */
@media (max-width: 1200px) {
  .api-client {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }
}

@media (max-width: 768px) {
  .method-url {
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .method-select,
  .url-input,
  .send-button {
    width: 100%;
  }
  
  .header-row {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .response-header {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }
  
  .response-meta {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }
  
  .body-header {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }
}

/* Loading animation */
@keyframes pulse {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

.send-button:disabled {
  animation: pulse 1.5s ease-in-out infinite;
}

/* Scrollbar styling */
.headers-display::-webkit-scrollbar,
.response-data::-webkit-scrollbar {
  width: 8px;
}

.headers-display::-webkit-scrollbar-track,
.response-data::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 4px;
}

.headers-display::-webkit-scrollbar-thumb,
.response-data::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 4px;
}

.headers-display::-webkit-scrollbar-thumb:hover,
.response-data::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}
