.json-prettifier {
  max-width: 1600px;
  margin: 0 auto;
}

.header {
  text-align: center;
  margin-bottom: 30px;
}

.header h2 {
  color: #333;
  font-size: 2.2rem;
  margin-bottom: 10px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.header p {
  color: #666;
  font-size: 1.1rem;
  line-height: 1.6;
}

.controls {
  display: flex;
  gap: 15px;
  margin-bottom: 30px;
  flex-wrap: wrap;
  justify-content: center;
}

.btn {
  padding: 12px 20px;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  gap: 8px;
  min-width: 140px;
  justify-content: center;
}

.btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn-primary {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.btn-primary:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(102, 126, 234, 0.4);
}

.btn-secondary {
  background: #6c757d;
  color: white;
}

.btn-secondary:hover:not(:disabled) {
  background: #5a6268;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(108, 117, 125, 0.4);
}

.btn-success {
  background: #28a745;
  color: white;
}

.btn-success:hover:not(:disabled) {
  background: #218838;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(40, 167, 69, 0.4);
}

.btn-info {
  background: #17a2b8;
  color: white;
}

.btn-info:hover:not(:disabled) {
  background: #138496;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(23, 162, 184, 0.4);
}

.btn-warning {
  background: #ffc107;
  color: #212529;
}

.btn-warning:hover:not(:disabled) {
  background: #e0a800;
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(255, 193, 7, 0.4);
}

.json-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 50px;
  margin-bottom: 30px;
  align-items: start;
}

.json-section {
  background: #f8f9fa;
  border-radius: 12px;
  padding: 30px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  min-width: 0;
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
  flex-shrink: 0;
}

.header-buttons {
  display: flex;
  gap: 10px;
  align-items: center;
}

.section-header h3 {
  color: #333;
  font-size: 1.3rem;
  margin: 0;
}

.copy-btn {
  background: #e9ecef;
  color: #495057;
  border: 1px solid #ced4da;
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 4px;
}

.copy-btn:hover:not(:disabled) {
  background: #dee2e6;
  transform: translateY(-1px);
}

.copy-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.expand-all-btn {
  background: #17a2b8;
  color: white;
  border: 1px solid #17a2b8;
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 4px;
}

.expand-all-btn:hover:not(:disabled) {
  background: #138496;
  transform: translateY(-1px);
}

.expand-all-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.collapse-all-btn {
  background: #6c757d;
  color: white;
  border: 1px solid #6c757d;
  padding: 6px 12px;
  border-radius: 6px;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 4px;
}

.collapse-all-btn:hover:not(:disabled) {
  background: #5a6268;
  transform: translateY(-1px);
}

.collapse-all-btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Textarea Container with Line Numbers */
.textarea-container {
  position: relative;
  display: flex;
  width: 100%;
  min-height: 500px;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  background: white;
  overflow: hidden;
}

.line-numbers {
  background: #f8f9fa;
  border-right: 1px solid #e1e5e9;
  padding: 25px 10px;
  font-family: 'Courier New', 'Monaco', monospace;
  font-size: 15px;
  line-height: 1.6;
  color: #6c757d;
  user-select: none;
  min-width: 50px;
  text-align: right;
  flex-shrink: 0;
}

.line-number {
  height: 1.6em;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding-right: 8px;
  transition: background-color 0.2s ease;
}

.line-number.error-line {
  background-color: #f8d7da;
  color: #721c24;
  font-weight: bold;
}

.json-textarea {
  width: 100%;
  min-height: 500px;
  padding: 25px;
  border: none;
  border-radius: 0;
  font-family: 'Courier New', 'Monaco', monospace;
  font-size: 15px;
  line-height: 1.6;
  resize: both;
  background: white;
  transition: none;
  min-width: 0;
  flex: 1;
  box-sizing: border-box;
  outline: none;
}

.textarea-container:focus-within {
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.textarea-container.error {
  border-color: #dc3545;
}

.textarea-container.error .line-numbers {
  background-color: #fff5f5;
  border-right-color: #dc3545;
}

.textarea-container.error .json-textarea {
  background-color: #fff5f5;
}

.json-textarea.formatted {
  background-color: #f8f9fa;
  border-color: #28a745;
}

.error-message {
  background: #f8d7da;
  color: #721c24;
  padding: 12px 15px;
  border-radius: 6px;
  margin-top: 10px;
  border: 1px solid #f5c6cb;
  font-size: 14px;
  line-height: 1.4;
}

.error-location {
  margin-top: 8px;
  padding-top: 8px;
  border-top: 1px solid #f5c6cb;
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.error-line, .error-column, .error-position {
  background: #dc3545;
  color: white;
  padding: 2px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: bold;
  font-family: 'Courier New', 'Monaco', monospace;
}

.toast-container {
  position: fixed;
  bottom: 20px;
  right: 20px;
  z-index: 1000;
}

.toast {
  background: #28a745;
  color: white;
  padding: 15px 20px;
  border-radius: 8px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  gap: 15px;
  min-width: 250px;
  animation: slideIn 0.3s ease-out;
}

.toast-body {
  flex: 1;
  font-size: 14px;
  font-weight: 500;
}

.toast-close {
  background: none;
  border: none;
  color: white;
  font-size: 18px;
  cursor: pointer;
  padding: 0;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transition: background-color 0.2s ease;
}

.toast-close:hover {
  background-color: rgba(255, 255, 255, 0.2);
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

/* Responsive Design */
@media (max-width: 768px) {
  .json-container {
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .controls {
    flex-direction: column;
    align-items: center;
  }
  
  .btn {
    width: 100%;
    max-width: 200px;
  }
  
  .section-header {
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
  }
  
  .textarea-container {
    min-height: 350px;
  }
  
  .line-numbers {
    padding: 20px 8px;
    font-size: 13px;
    min-width: 45px;
  }
  
  .json-textarea {
    min-height: 350px;
    font-size: 13px;
    padding: 20px;
  }
  
  .toast-container {
    bottom: 10px;
    right: 10px;
    left: 10px;
  }
  
  .toast {
    min-width: auto;
  }
}

@media (max-width: 480px) {
  .header h2 {
    font-size: 1.8rem;
  }
  
  .header p {
    font-size: 1rem;
  }
  
  .json-section {
    padding: 15px;
  }
  
  .textarea-container {
    min-height: 300px;
  }
  
  .line-numbers {
    padding: 15px 6px;
    font-size: 12px;
    min-width: 40px;
  }
  
  .json-textarea {
    min-height: 300px;
    padding: 15px;
    font-size: 12px;
  }
}

/* JSON Display Styles */
.json-display {
  width: 100%;
  min-height: 500px;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  background: white;
  overflow: hidden;
  flex: 1;
  box-sizing: border-box;
}

.json-panel-container {
  display: flex;
  height: 100%;
  min-height: 500px;
}

.json-control-panel {
  width: 60px;
  background: #f8f9fa;
  border-right: 1px solid #e1e5e9;
  padding: 25px 10px;
  font-family: 'Courier New', 'Monaco', monospace;
  font-size: 15px;
  line-height: 1.6;
  overflow-y: auto;
  flex-shrink: 0;
}

.json-content-panel {
  flex: 1;
  padding: 25px;
  font-family: 'Courier New', 'Monaco', monospace;
  font-size: 15px;
  line-height: 1.6;
  overflow: auto;
  background: white;
}

.control-line {
  height: 1.6em;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 2px 0;
}

.json-placeholder {
  color: #999;
  font-style: italic;
  text-align: center;
  padding: 50px 20px;
}

.json-renderer {
  white-space: pre-wrap;
  word-break: break-word;
}

/* Collapse Button */
.collapse-btn {
  background: #667eea;
  color: white;
  border: none;
  border-radius: 3px;
  width: 20px;
  height: 20px;
  font-size: 12px;
  font-weight: bold;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.collapse-btn:hover {
  background: #5a6fd8;
  transform: scale(1.1);
}

/* Control panel specific styles */
.json-control-panel .collapse-btn {
  margin: 0;
}

/* JSON Syntax Highlighting */
.json-object, .json-array {
  display: block;
  margin: 0;
}

.json-brace, .json-bracket {
  color: #333;
  font-weight: bold;
}

.json-key {
  color: #0066cc;
  font-weight: bold;
}

.json-string {
  color: #008000;
}

.json-number {
  color: #ff6600;
  font-weight: bold;
}

.json-boolean {
  color: #cc0066;
  font-weight: bold;
}

.json-null {
  color: #999;
  font-style: italic;
}

.json-colon {
  color: #333;
}

.json-comma {
  color: #333;
}

.json-indent {
  color: transparent;
  user-select: none;
  white-space: pre;
}

.json-collapsed {
  color: #666;
  font-style: italic;
  margin-left: 5px;
}

.json-error {
  color: #dc3545;
  font-weight: bold;
}

.json-content {
  display: block;
  margin: 0;
}

.json-item {
  display: block;
  margin: 0;
  padding-left: 20px;
}

.json-bracket-line, .json-brace-line {
  display: block;
  margin: 0;
}

/* Minified JSON Display */
.json-minified-container {
  width: 100%;
  height: 100%;
  min-height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  box-sizing: border-box;
}

.json-minified {
  width: 100%;
  max-width: 100%;
  background: #f8f9fa;
  border-radius: 6px;
  border: 1px solid #e9ecef;
  padding: 20px;
  box-sizing: border-box;
}

.json-minified-content {
  font-family: 'Courier New', 'Monaco', monospace;
  font-size: 14px;
  line-height: 1.4;
  word-break: break-all;
  white-space: pre-wrap;
  color: #333;
  background: white;
  padding: 15px;
  border-radius: 4px;
  border: 1px solid #dee2e6;
  display: block;
  width: 100%;
  box-sizing: border-box;
  overflow-x: auto;
}



/* Responsive adjustments for JSON display */
@media (max-width: 768px) {
  .json-display {
    min-height: 350px;
  }
  
  .json-panel-container {
    min-height: 350px;
  }
  
  .json-control-panel {
    width: 50px;
    padding: 20px 8px;
    font-size: 13px;
  }
  
  .json-content-panel {
    padding: 20px;
    font-size: 13px;
  }
  
  .collapse-btn {
    width: 18px;
    height: 18px;
    font-size: 11px;
  }
  
  .header-buttons {
    flex-direction: column;
    gap: 5px;
  }
  
  .json-minified-container {
    min-height: 350px;
    padding: 15px;
  }
  
  .json-minified {
    padding: 15px;
  }
  
  .json-minified-content {
    font-size: 12px;
    padding: 12px;
  }
}

@media (max-width: 480px) {
  .json-display {
    min-height: 300px;
  }
  
  .json-panel-container {
    min-height: 300px;
  }
  
  .json-control-panel {
    width: 45px;
    padding: 15px 6px;
    font-size: 12px;
  }
  
  .json-content-panel {
    padding: 15px;
    font-size: 12px;
  }
  
  .collapse-btn {
    width: 16px;
    height: 16px;
    font-size: 10px;
  }
  
  .json-minified-container {
    min-height: 300px;
    padding: 10px;
  }
  
  .json-minified {
    padding: 10px;
  }
  
  .json-minified-content {
    font-size: 11px;
    padding: 10px;
  }
}
